.masters-handbook-page {
  background: #fdfaf7;
}

.mh-hero-section {
  background: linear-gradient(180deg, #fff7f0 0%, #fdfaf7 100%);
  padding: 4rem 0;
}

.mh-hero-copy {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.mh-hero-title {
  font-size: clamp(2rem, 3vw, 3.2rem);
  font-weight: 700;
  color: #2c3e50;
  line-height: 1.2;
}

.mh-hero-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.35rem 0.85rem;
  border-radius: 999px;
  background: #d4f1f6;
  color: #2c3e50;
  font-weight: 600;
  margin-bottom: 1rem;
  font-size: 0.85rem;
}

.mh-hero-subtitle {
  margin-top: 1rem;
  color: #5a6c7d;
  font-size: 1.05rem;
  line-height: 1.6;
}

.mh-primary-btn {
  background-color: #f48438 !important;
  color: #fff !important;
  font-weight: 600 !important;
  text-transform: none !important;
  border-radius: 999px !important;
  padding: 0.75rem 1.5rem !important;
  box-shadow: none !important;
}

.mh-primary-btn:hover {
  background-color: #e56a1a !important;
}

.mh-hero-illustration {
  position: relative;
  padding: 2rem;
  background: #fff;
  border-radius: 32px;
  box-shadow: 0 20px 60px rgba(32, 56, 85, 0.08);
}

.mh-hero-floating {
  position: absolute;
  background: #fff;
  border-radius: 999px;
  padding: 0.4rem 0.9rem;
  font-size: 0.8rem;
  font-weight: 600;
  color: #2c3e50;
  box-shadow: 0 10px 35px rgba(32, 56, 85, 0.15);
}

.mh-hero-floating.badge-0 {
  top: 15%;
  right: 10%;
}

.mh-hero-floating.badge-1 {
  top: 40%;
  right: -5%;
}

.mh-hero-floating.badge-2 {
  bottom: 15%;
  left: 5%;
}

.mh-hero-illustration img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.mh-highlight-section {
  padding: 4rem 0;
}

.mh-highlight-card {
  border-radius: 24px !important;
  box-shadow: 0 12px 30px rgba(32, 56, 85, 0.08);
  padding: 1.75rem;
}

.mh-number-chip {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 2rem;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.mh-card-title {
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.mh-card-text {
  color: #51606f;
  line-height: 1.5;
}

.mh-trust-card {
  background: #fff;
  border-radius: 32px;
  padding: 2.5rem;
  box-shadow: 0 20px 60px rgba(32, 56, 85, 0.08);
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.mh-trust-item {
  align-items: flex-start;
  gap: 1rem;
  padding-left: 0 !important;
}

.mh-trust-icon {
  width: 48px;
  height: 48px;
  border-radius: 16px;
  background: #f3f6f9;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mh-whats-inside-section {
  padding: 4rem 0;
}

.mh-whats-inside-card {
  background: linear-gradient(135deg, #7866ff 0%, #9b7bff 60%, #b594ff 100%);
  border-radius: 32px;
  padding: 2.5rem;
  color: #fff;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mh-whats-inside-card h2 {
  font-size: clamp(2rem, 2.5vw, 2.8rem);
  font-weight: 700;
}

.mh-inside-row {
  background: #fff;
  border-radius: 24px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  box-shadow: 0 12px 30px rgba(32, 56, 85, 0.08);
}

.mh-inside-row h6 {
  margin: 0;
  font-weight: 600;
  color: #2c3e50;
}

.mh-inside-row p {
  color: #5a6c7d;
  margin: 0.3rem 0 0;
}

.mh-link-btn {
  text-transform: none !important;
  font-weight: 600 !important;
  color: #2c3e50 !important;
}

.mh-link-btn svg {
  transition: transform 0.2s ease;
}

.mh-link-btn:hover svg {
  transform: translateX(3px);
}

.mh-faq-section {
  padding: 4rem 0 5rem;
}

.mh-section-title {
  font-size: clamp(1.8rem, 2.4vw, 2.6rem);
  font-weight: 700;
  color: #2c3e50;
}

.mh-faq-item {
  background: #fff;
  border-radius: 30px;
  padding: 1rem 1.5rem;
  box-shadow: 0 10px 30px rgba(32, 56, 85, 0.08);
  cursor: pointer;
  transition: all 0.2s ease;
}

.mh-faq-item.active {
  box-shadow: 0 12px 40px rgba(32, 56, 85, 0.12);
}

.mh-faq-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.mh-faq-icon {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: #eef0ff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3c3f5c;
}

.mh-contact-card {
  background: #fdf2e9;
  border-radius: 32px;
  padding: 2.5rem;
  box-shadow: 0 15px 40px rgba(244, 132, 56, 0.2);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mh-contact-icon {
  font-size: 40px !important;
  color: #f48438;
}

@media (max-width: 900px) {
  .mh-hero-section {
    padding: 3rem 0;
  }

  .mh-hero-copy {
    align-items: center;
    text-align: center;
  }

  .mh-hero-badge {
    margin-bottom: 0.5rem;
  }

  .mh-hero-illustration {
    margin-top: 1.5rem;
  }

  .mh-hero-floating.badge-1 {
    right: 5%;
  }

  .mh-trust-card,
  .mh-whats-inside-card,
  .mh-contact-card {
    padding: 2rem;
  }

  .mh-inside-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .mh-faq-item {
    padding: 1rem;
  }
}

@media (max-width: 600px) {
  .mh-hero-section {
    padding: 2.5rem 0;
  }

  .mh-hero-title {
    font-size: 2rem;
  }

  .mh-hero-illustration {
    padding: 1.5rem;
  }

  .mh-hero-floating {
    display: none;
  }

  .mh-highlight-card {
    padding: 1.25rem;
  }

  .mh-trust-card {
    padding: 1.75rem;
    text-align: center;
  }

  .mh-trust-item {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .mh-inside-row {
    padding: 1.25rem;
  }

  .mh-contact-card {
    padding: 1.75rem;
    text-align: center;
  }

  .mh-contact-card .mh-primary-btn {
    align-self: center;
  }
}

